@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header--container {
  height: 100px;
  border-bottom: 6px solid rgb(163, 163, 159);
}
.header--wrapper {
  display: flex;
  width: 1180px;
  margin: 0 auto;
}
.header--spa {
  width: 250px;
  object-fit: contain;
  object-position: center;
  height: 100px;
}
.header--links-nav {
  width: 100%;
}
.header--links-ul {
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header--links-li {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25%;
  height: 50%;
  border: 4px solid rgb(25, 173, 75);
  border-radius: 50px 0;
  list-style: none;
  text-align: center;
  transition: 0.7s;
}

.header--links-li:hover {
  transition: 0.7s;
  color: white;
  cursor: pointer;
  background-color: rgb(163, 163, 159);
}
.header--button {
  font-family: "Yusei Magic", sans-serif;
  font-weight: 300;
  width: 100%;
  text-decoration: none;
  color: black;
  transition: 0.8s;
}
.header--button:hover {
  min-width: 100%;
  transition: 0.8s;
  color: white;
}

.footer--container {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px 0 10px;
  }
  .footer--container-cards {
    display: flex;
    justify-content: space-between;
    width: 150px;
  }
  .footer--text {
    font-size: 16px;
  }
  .footer--cards {
    width: 32px;
    height: 22px;
    object-fit: cover;
  }
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}



